import React from 'react'

import { render } from '@testing-library/react-native'
import { Text } from 'components'

import { CenterView } from './CenterView'

describe('CenterView Component', () => {
  it('renders children correctly', () => {
    const { getByText } = render(
      <CenterView>
        <Text>Centered Content</Text>
      </CenterView>,
    )

    const centeredContent = getByText('Centered Content')

    // Check if the children are rendered correctly
    expect(centeredContent).toBeTruthy()
  })

  it('applies flex prop correctly', () => {
    const flexValue = 2
    const { getByTestId } = render(
      <CenterView flex={flexValue} testID="center-view" />,
    )

    const centerView = getByTestId('center-view')

    // Check if the flex prop is applied correctly
    expect(centerView).toHaveStyle({ flex: flexValue })
  })

  it('applies custom styles correctly', () => {
    const customStyles = {
      backgroundColor: 'blue',
    }

    const { getByTestId } = render(
      <CenterView style={customStyles} testID="center-view" />,
    )

    const centerView = getByTestId('center-view')

    // Check if custom styles are applied
    expect(centerView).toHaveStyle(customStyles)
  })
})
